
.controls a{
    transition: all .15s linear 0s;
    display: inline-block;
    padding: 15px 25px;
    background-color:#faebd7;
    
    font-size: 12px;
    color:#000;
    font-weight:800;
    font-family: arial;
    letter-spacing: 1px;

    box-shadow: -3px 3px 0 #000;
    text-decoration: none;
    border-radius: 16px;
}

.intend {
    position: relative;
    z-index: 99999;
    text-align: center;
    padding: 10px;
}

.dialog{
    background-color: #faebd7;
    font-size: 20px;
    padding: 10px;
    border-radius: 16px;
    box-shadow: -3px 3px 0 #000;
    -webkit-hyphens: auto;
    -ms-hyphens: auto;
    hyphens: auto;
}



.yesButton {

    font-size: 18px;
    margin-top: 10px;
    transition: all .15s linear 0s;
    display: inline-block;
    padding: 15px 25px;
    background-color:#32CD32;
    
    color:#000;
    font-weight:800;
    font-family: arial;
    letter-spacing: 1px;

    box-shadow: -3px 3px 0 #000;
    text-decoration: none;
    border-radius: 16px;
    
}

